import React, {  Component } from 'react';
import {  Grid, Cell, Textfield, Button, Card } from 'react-mdl';
import { Link } from 'react-router-dom';

class Landing extends Component {
  render(){
    return(
      <div>
        <Grid className="hero-section center-align">
          <div  className="transbox center-align">
            <Cell col={12} className="center-align">
            {/* Textfield with floating label */}
              <div className="form">
              <Card shadow={0} style={{padding: '50px', margin: 'auto'}}>
                <form>
                  <div>
                  <h5 style={{color: '#3F51B5'}}>ADMIN LOGIN</h5>
                    <Textfield
                        onChange={() => {}}
                        label="Username..."
                        floatingLabel
                        style={{width: '100%'}}
                    />
                  </div>

                  {/* Numeric Textfield with floating label */}
                  <div>
                    <Textfield
                        onChange={() => {}}
                        Type="password"
                        pattern="-?[0-9]*(\.[0-9]+)?"
                        error="Password should be numbers"
                        label="Password..."
                        floatingLabel
                        style={{width: '100%'}}
                    />
                  </div>
                  <Link to="/aboutme"><Button raised colored ripple style={{width: '100%'}}>LOGIN</Button></Link>
                </form>
                </Card>
              </div>
            </Cell>
          </div>
        </Grid>
      </div>
    );
  }
}

export default Landing;
