import React, {  Component } from 'react';
import {  Grid, Cell, Textfield, Button, Card, Table, TableHeader } from 'react-mdl';

class About extends Component {
  render(){
    return(
      <div>
      <div>
        <Grid className="center-align">
          <div  className=" center-align">
            <Cell col={12} className="center-align">
            {/* Textfield with floating label */}
              <div>
              <Table
    sortable
    style={{width: '100%'}}
    shadow={0}
    rows={[
        {FullName: 'Festus Folarin', Nationality: 'NIGERIA', Gender: 'MALE', PassportNo: 'A06055319', CheckIn: '10:30PM', CheckOut: '11:30AM'},
        {FullName: 'Jon Doe', Nationality: 'USA', Gender: 'MALE', PassportNo: 'A06055319', CheckIn: '10:30PM', CheckOut: '11:30AM'},
        {FullName: 'Jane Doe', Nationality: 'CANADA', Gender: 'FEMALE', PassportNo: 'A06055319', CheckIn: '10:30PM', CheckOut: '11:30AM'}
    ]}
>
    <TableHeader
        name="FullName"
        tooltip="Guest's Full Name as in Passport"
    >
        Full Name
    </TableHeader>
    <TableHeader
        numeric
        name="Nationality"
        tooltip="Guest's Nationality"
    >
        Nationality
    </TableHeader>
    <TableHeader
        numeric
        name="Gender"
        tooltip="Guest's Gender"
    >
        Gender
    </TableHeader>
    <TableHeader
        numeric
        name="PassportNo"
        tooltip="Guest's Passport Number"
    >
        Passport No
    </TableHeader>
    <TableHeader
        numeric
        name="CheckIn"
        tooltip="Time of Check-in"
    >
        Check-in
    </TableHeader>
    <TableHeader
        numeric
        name="CheckOut"
        tooltip="Time of Check-out"
    >
        Check-out
    </TableHeader>
</Table>
              </div>
            </Cell>
          </div>
        </Grid>
      </div>
      </div>
    )
  }
}

export default About;
